<template>
  <section class="editor">
    <Modal :text="modalText" v-if="showModal" @close="showModal = false" @delete="deleteNote" @save="saveNote" @reset="resetNote"></Modal>
    <div class="editor__wrapper">
      <div class="warning">
        <p v-show="warning" class="warning_message">{{ warning }}</p>
      </div>
      <div class="edit__name">
        <h2 class="edit__name_title">Название заметки: </h2>
        <input type="text" class="edit__name_input" :placeholder="note.title" v-model="note.title"/>
      </div>
      <div class="edit__todos">
        <h3 class="edit__todos_title">TODOs: </h3>
        <ul class="edit__todos_list">
          <li v-for="(todo, index) in note.todos || 1" :key="index" class="todos__list_item">
            <div class="list__item_inner">
              <img src="../assets/trash.svg" alt="delete_icon" @click="deleteTODO(index)">
              <input type="checkbox" :checked="todo.completed" v-model="todo.completed">
              <input type="text" class="list__item_input" :placeholder="todo.title" v-model="todo.title">
            </div>
          </li>
        </ul>
      </div>
      <button class="add__new_todo" @click="addTODO">Добавить еще TODO</button>
      <div class="editor__toolbox">
        <button class="save_note" @click="saveNoteQuestion">Сохранить</button>
        <button class="reset_note" @click="resetNoteQuestion">Отменить редактирование</button>
        <button class="delete_note" @click="deleteNoteQuestion">Удалить</button>
      </div>
      <router-link to="/"><button class="add__new_todo">Назад</button></router-link>
    </div>
  </section>
</template>

<script>
  import Modal from "@/components/Modal";
  export default {
    name: "Editor",
    components:{
      Modal
    },
    props:['noteProp'],
    data(){
      return{
        warning: '',
        showModal: false,
        note: this.noteProp || {
          _id: localStorage.maxId || 1,
          title: '',
          todos:[{}],
          completed: false
        },
        modalText: {},
        pure_note: {}
      }
    },
    created() {
      this.pure_note = JSON.parse(JSON.stringify(this.note))
    },
    methods:{
      addTODO(){
        if (this.note.todos[this.note.todos.length - 1].title) this.note.todos.push({})
        else this.warning = 'Заполните пустой TODO'
      },
      deleteTODO(index){
        if (this.note.todos.length > 1) this.note.todos.splice(index, 1);
      },
      resetNoteQuestion(){
        this.modalText ={
          type: 'reset',
          title: 'Обновить заметку?',
          subtitle: 'Осторожно! Заметка будет обновлена до начального состояния!',
          apply: 'Обновить',
          discard: 'Назад'
        };
        this.showModal = true;
      },
      deleteNoteQuestion(){
        this.modalText ={
          type: 'delete',
          title: 'Удалить заметку?',
          subtitle: 'Осторожно! Заметка удалится навсегда!',
          apply: 'Удалить',
          discard: 'Назад'
        };
        this.showModal = true;

      },
      saveNoteQuestion(){
        this.modalText ={
          type: 'save',
          title: 'Сохранить заметку?',
          subtitle: 'Осторожно! Все данные сейчас обновятся!',
          apply: 'Сохранить',
          discard: 'Назад'
        };
        this.showModal = true;
      },
      saveNote(){
        if (this.validation() === true){
          let notesArray = JSON.parse(localStorage.notes);
          let index = notesArray.findIndex(item => item._id === this.note._id);
          if (index != -1) notesArray[index] = this.note;
          else notesArray.push(this.note);
          localStorage.notes = JSON.stringify(notesArray);
          localStorage.maxId++;
          console.log(localStorage.notes);
          this.$router.push('/')
        }else this.showModal = false;

      },
      deleteNote(){
        if (!localStorage.notes) localStorage.notes = JSON.stringify([]);
        let notesArray = JSON.parse(localStorage.notes);
        let index = notesArray.findIndex(item => item._id === this.note._id);
        if (index != -1) {
          notesArray.splice(index, 1)
          localStorage.notes = JSON.stringify(notesArray);
          this.$router.push('/')
        }
        else {
          this.warning = "Вы не можете удалить этот элемент!"
          this.showModal = false
        }
      },
      resetNote(){
        this.note = JSON.parse(JSON.stringify(this.pure_note));
        this.showModal = false
      },
      validation(){
        console.log(this.note.todos);
        if (this.note.title === ''){
          this.warning = 'Обязательно ввести название заметки!'
        }else{
          if (!this.note.todos[0].title) this.warning = 'Обязательно нужно добавить хотя бы одно TODO!'
          else return true;
        }
      }
    }
  }
</script>

